@include option("form") {

select {
	cursor: pointer;

	&[multiple] {
		padding: 0 $input-padding-horizontal;
        resize: vertical;
        transition-property: border;
        scrollbar-width: initial;
        scrollbar-color: initial;

        &:active {
            color: var(--body-color);
        }

		option{
			padding: $select-option-padding;
			border-top: $input-border-width solid var(--input-border-color);

			&:first-child {
				border:none;
				margin-top: $input-padding-horizontal/2;
			}

			&:last-child {
				margin-bottom: $input-padding-horizontal/2;
			}
		}
	}
}
select:not([multiple]) {
    padding-right: 2.5rem;
    color: var(--body-color);
    background-position: right 1rem center;
    background-repeat: no-repeat;
    background-size: 1.2rem;
}

select:not([multiple]) {
    background-image: url("data:image/svg+xml,%3C%3Fxml%20version%3D%221.0%22%20standalone%3D%22no%22%3F%3E%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20xmlns%3Axlink%3D%22http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink%22%20style%3D%22isolation%3Aisolate%22%20viewBox%3D%220%200%2020%2020%22%20width%3D%2220%22%20height%3D%2220%22%3E%3Cpath%20d%3D%22%20M%209.96%2011.966%20L%203.523%205.589%20C%202.464%204.627%200.495%206.842%201.505%207.771%20L%201.505%207.771%20L%208.494%2014.763%20C%209.138%2015.35%2010.655%2015.369%2011.29%2014.763%20L%2011.29%2014.763%20L%2018.49%207.771%20C%2019.557%206.752%2017.364%204.68%2016.262%205.725%20L%2016.262%205.725%20L%209.96%2011.966%20Z%20%22%20fill%3D%22000%22%2F%3E%3C%2Fsvg%3E");
