@include option("media") {

[class*="#{class("Media")}"] {
	display: flex;
	flex-direction: row;
	align-items: flex-start;

    @include option("media-responsive") {
        @include respond-to("tiny", "down") {
    		flex-direction: column;
            align-items: center;

            .#{class("Media-child")}#{class("(media)-figure")} {
                margin: 0 0 $media-figure-margin-right 0;
            }
    	}
    }
}

.#{class("Media-child")}#{class("(media)-body")} {
	flex: 1;

    &:last-child {
        margin-bottom: 0;
    }
}

.#{class("Media-child")}#{class("(media)-figure")} {
    order: -1;

    &:not(.#{class("item-last")}) {
        margin-right: $media-figure-margin-right;
    }
    
    &.#{class("item-last")} {
        margin-left: $media-figure-margin-right;
    }
}

}
